<template>
<div class="payback-page-content">
  <div class="header-content">
    <page-title :title="pageName"></page-title>
  </div>
  <div class="main-content">
    <div class="block-content-table">
      <min-title nameVal="回款概览-店铺"  :show="'3'"></min-title>
      <table-list :tableData="hkglTableData"></table-list>
    </div>
    <div class="block-content-table">
      <min-title nameVal="回款分析-店铺"  :tabsData="blockSwitchList" :show="'1'" :type="'payback-hkfx'" @tabsType="tabsType"></min-title>
      <table-list :tableData="hkfxTableData" :type="'payback-hkfx'" :needClick="false" :default-selected="true"
                  @changeTableOrder="changeTableOrder"></table-list>
    </div>
    <div class="block-content-chart">
      <min-title :nameVal="ydjdTitle"  :show="'3'"></min-title>
      <div class="block-chart">
        <BarLineChart :chart-data="ydjdLineData" :option="ydjdLineOption"></BarLineChart>
      </div>
    </div>
    <div class="block-content-chart">
      <min-title :nameVal="qsfxTitle"  :show="'1'" :type="'payback-qsfx'" :tabsData="qsfxTabList" @tabsType="tabsType"></min-title>
      <div class="block-chart">
        <BarLineChart :chart-data="qsfxLineData" :option="getYzyBarOption(qsfxLineData)"></BarLineChart>
      </div>
    </div>

  </div>
</div>
</template>

<script>
