          /* {dp: '水星一店', xe: '1600', mb: '1700', tb: '35.4', dc: '13.6', zb: '30.7'},
          {dp: '水星二店', xe: '1800', mb: '1200', tb: '-25.4', dc: '-13.6', zb: '20.7'},
          {dp: '水星三店', xe: '1400', mb: '1230', tb: '-55.4', dc: '20.6', zb: '40.7'},
          {dp: '水星四店', xe: '1200', mb: '1800', tb: '65.4', dc: '23.6', zb: '50.7'} */
        ]
