      qsfxIndex: 0,
      ydjdLineData: [
        /* {label: '1', mb: '10', sj: '5'},
        {label: '2', mb: '20', sj: '15'},
        {label: '3', mb: '30', sj: '27'},
        {label: '4', mb: '40', sj: '38'},
        {label: '5', mb: '50', sj: '38'},
        {label: '6', mb: '55', sj: '38'},
        {label: '7', mb: '60', sj: '38'},
        {label: '8', mb: '65', sj: ''},
        {label: '9', mb: '70', sj: ''},
        {label: '10', mb: '80', sj: ''},
        {label: '11', mb: '90', sj: ''},
        {label: '12', mb: '100', sj: ''} */],
      qsfxLineData: [
        /* {label: '1', xs: '15', hk: '25'},
        {label: '2', xs: '55', hk: '45'},
        {label: '3', xs: '15', hk: '25'},
        {label: '4', xs: '55', hk: '65'},
        {label: '5', xs: '15', hk: '25'},
        {label: '6', xs: '35', hk: '85'} */
      ],
      qsfxLineDataInit: [
        /*  {label: '1', xsdc: '15', hkdc: '25', xsje: '1200', hkje: '1000'},
        {label: '2', xsdc: '25', hkdc: '55', xsje: '1100', hkje: '1600'},
        {label: '3', xsdc: '55', hkdc: '45', xsje: '1400', hkje: '1700'},
        {label: '4', xsdc: '25', hkdc: '65', xsje: '1600', hkje: '1300'},
        {label: '5', xsdc: '75', hkdc: '55', xsje: '1800', hkje: '1200'},
        {label: '6', xsdc: '35', hkdc: '75', xsje: '1200', hkje: '1000'} */
      ]
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    },
    selectedZgsName () {
      const authList = this.$store.state.authList
      let str = ''
      if (this.userIdentityInit === '2') {
        for (let item of authList) {
          if (item.shop === this.selectedDpName) {
            str = item.role
            break
          }
        }
      } else if (this.userIdentityInit === '3') {
        for (let item of authList) {
          if (item.role === this.selectedDpName) {
            str = item.zgs
            break
          }
        }
      }
      return str
    },
    ydjdLineOption () {
      const option = {
        color: ['#326DFF', '#D1A226'],
        'legend': {
          show: true,
          selectedMode: false,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '达成',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: '实际',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          },
          {
            type: 'line',
            name: '目标',
            yAxisIndex: 0,
            symbol: 'none',
            smooth: true,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          }]
      }
      const labelList = []
      const sjList = []
      const mbList = []
      for (let item of this.ydjdLineData) {
        labelList.push(item.label)
        sjList.push(item.sj)
        mbList.push(item.mb)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = sjList
      option.series[1].data = mbList
      return option
    }
  },
  methods: {
    getOptionA (option, opt, dataArr) {
      option = {
        color: ['#326DFF', '#D1A226', '#FF0200'],
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': this.qsfxIndex === 0 ? '达成' : '金额',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: this.qsfxIndex === 0 ? '{value}%' : '{value}'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: this.qsfxIndex === 0 ? '销售达成' : '销售金额',
            symbol: 'none',
            smooth: true,
            yAxisIndex: 0,
            lineStyle: {
              color: '#D1A226',
              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          },
          {
            type: 'line',
            name: this.qsfxIndex === 0 ? '回款达成' : '回款金额',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          },
          {
            type: 'line',
            symbol: 'none',
            name: this.qsfxIndex === 0 ? '年累计回款达成' : '年累计回款金额',
            yAxisIndex: 0,
            lineStyle: {
              color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const xsdcList = []
      const hkList = []
      const nljcList = []
      for (let item of dataArr) {
        labelList.push(item.label)
        xsdcList.push(item.xsdc)
        hkList.push(item.hk)
        nljcList.push(item.nljdc)
      }

      option.xAxis[0].data = labelList
      option.series[0].data = xsdcList
      option.series[1].data = hkList
      if (this.qsfxIndex === 0) {
        option.series[2].data = nljcList
      }

      return option
    },
    getOptionB (option, opt, dataArr) {
      option = {
        color: ['#326DFF', '#D1A226', '#FF0200'],
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': this.qsfxIndex === 0 ? '达成' : '金额',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: this.qsfxIndex === 0 ? '{value}%' : '{value}'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: this.qsfxIndex === 0 ? '销售达成' : '销售金额',
            symbol: 'none',
            smooth: true,
            yAxisIndex: 0,
            lineStyle: {
              color: '#D1A226',
              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          },
          {
            type: 'line',
            name: this.qsfxIndex === 0 ? '回款达成' : '回款金额',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          }
        ]
      }
      const labelList = []
      const xsdcList = []
      const hkList = []
      const nljcList = []
      for (let item of dataArr) {
        labelList.push(item.label)
        xsdcList.push(item.xsdc)
        hkList.push(item.hk)
        nljcList.push(item.nljdc)
      }

      option.xAxis[0].data = labelList
      option.series[0].data = xsdcList
      option.series[1].data = hkList

      return option
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'payback-hkfx') {
        tempTable = _.cloneDeep(this.hkfxTableData)
      } else if (type === 'payback-jgfx') {
        tempTable = _.cloneDeep(this.jgfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'payback-hkfx') {
        this.hkfxTableData.tableHeader = headerList
        this.hkfxTableData.tableContent = tableContent
      } else if (type === 'payback-jgfx') {
        this.jgfxTableData.tableHeader = headerList
        this.jgfxTableData.tableContent = tableContent
      }
    },
    tabsType ({activeValue, name, type, index}) {
      if (type === 'payback-hkfx') {
        this.hkfxIndex = index
        this.dealDpHkfxTableData()
      } else if (type === 'payback-qsfx') {
        this.qsfxIndex = index
        this.dealDpQsfxLineData()
      }
    },
    getYzyBarOption (dataArr, opt) {
      let option = {}
      if (this.qsfxIndex === 0) {
        option = this.getOptionA(option, opt, dataArr)
      } else {
        option = this.getOptionB(option, opt, dataArr)
      }
      return option
    },
    // 下面是查询数据的方法
    getAllData () {
      this.getDpHkglTableData(this.selectedZgsName, this.selectedDpName)
      this.getDpHkfxTableData(this.selectedZgsName)
    },
    async getDpHkglTableData (companyName, dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.paybackShop.getDpHkglTableData,
        {dateStrD: this.dateStr, subName: companyName, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.time = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.dc = '--'
            obj.tb = '--'
            obj.hk = formatNumber(initNum(item.re_amt), 2)
          } else {
            obj.mb = formatNumber(initNum(item.re_target_amt), 2)
            obj.hk = formatNumber(initNum(item.re_amt), 2)
            obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
            obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
          }
          list.push(obj)
        }
        if (list.length > 0) {
          this.hkglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '1', isPresent: false, needArrow: false},
              {name: '回款', prop: 'hk', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true}
            ],
            tableContent: list.sort(compareSort('mb', true))
          }
        } else {
          this.hkglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '1', isPresent: false, needArrow: false},
              {name: '回款', prop: 'hk', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true}
            ],
            tableContent: []
          }
        }
      }
    },
    // 查询 店铺 销售分析 表格数据
    async getDpHkfxTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.paybackShop.getDpHkfxTableData,
        {dateStrM: this.dateStrM, subName: companyName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.hkfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealDpHkfxTableData()
      }
    },
    dealDpHkfxTableData () {
      const list = []
      let dataList = []
      if (this.hkfxIndex === 0) {
        dataList = this.hkfxTableDataInit.resM || []
      } else if (this.hkfxIndex === 1) {
        dataList = this.hkfxTableDataInit.resJ || []
      } else if (this.hkfxIndex === 2) {
        dataList = this.hkfxTableDataInit.resNl || []
      } else if (this.hkfxIndex === 3) {
        dataList = this.hkfxTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.dp = item.customer_name
        obj.dcpm = formatNumber(initNum(item.order_yield_rate), 0)
        obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
        obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
        obj.tbpm = formatNumber(initNum(item.order_re_amt_yoy), 0)
        list.push(obj)
      }
      const headerList = [
        {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '达成', prop: 'dc', needOrder: true, order: '2', isPresent: true, needArrow: false},
        {name: '达成排名', prop: 'dcpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '同比排名', prop: 'tbpm', needOrder: true, order: '0', isPresent: false, needArrow: false}
      ]
      this.hkfxTableData = {
        tableHeader: headerList,
        tableContent: list.sort(compareSort('dc', false))
      }
      // 设置第一行被选中， 从而查询下面线图和表格数据
      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.hkfxTableData.tableContent.length > 0) {
        this.qsfxTitle = '趋势分析-' + this.selectedDpName
        this.ydjdTitle = '月度进度-' + this.selectedDpName
        // 这里要设置表格第一行默认选中
        this.$eventBus.$emit('resetSelectedRow', {type: 'payback-hkfx', name: this.selectedDpName})
        // 店铺月度进度
        this.getDpYdjdTableData(this.selectedDpName)
        // 店铺趋势分析
        this.getDpQsfxTableData(this.selectedDpName)
      } else {
        this.qsfxTitle = '趋势分析'
        this.ydjdTitle = '月度进度'
        this.ydjdLineData = []
        this.qsfxLineData = []
      }
    },
    async getDpYdjdTableData (dpName) {
      let params = {
        dateStrD: this.dateStr,
        dateNum: getMonthDayNum(this.dateStr),
        cusName: dpName
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.paybackShop.getDpYdjdTableData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.period_wid
          obj.mb = initNum(item.target_yield_rate) * 100
          obj.sj = initNum(item.yield_rate) * 100
          list.push(obj)
        }
        if (list.length > 0) {
          this.ydjdLineData = list
        } else {
          this.ydjdLineData = []
        }
      }
    },
    async getDpQsfxTableData (dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.paybackShop.getDpQsfxTableData,
        {dateStrM: this.dateStrM, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.qsfxLineDataInit = result.res
        this.dealDpQsfxLineData()
      }
    },
    dealDpQsfxLineData (companyName) {
      const list = []
      let dataList = []
      dataList = this.qsfxLineDataInit || []
      if (this.qsfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.yield_rate) * 100
          obj.hk = initNum(item.re_yield_rate) * 100
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      } else if (this.qsfxIndex === 1) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.sale_amt)// formatNumber(initNum(item.sale_amt).toFixed(0), 0)
          obj.hk = initNum(item.re_amt)// formatNumber(initNum(item.re_amt).toFixed(0), 0)
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      }
      this.qsfxLineData = list
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '回款')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '店铺', URL: '回款'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  components: {
    pageTitle,
    MinTitle,
    BarLineChart,
    TableList
  }
}
</script>

